.form-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.5rem; }

.form {
  width: 80%;
  margin: 0 auto;
  padding: 1rem 2rem 2.5rem;
  box-shadow: 1px 3px 6px #ddd;
  background: #efefef;
  display: flex;
  flex-direction: column; }
  .form label {
    padding: 1.5rem 0;
    display: block;
    font-size: 1.4rem; }
  .form input, .form textarea {
    width: 100%;
    padding: 1rem;
    border: 1px solid #e6e6e6; }
  .form .btn {
